body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    padding-top: 140px;
}

.footer {
    height: 20px;
    width: 100%;
    line-height: 20px;
    font-size: 12px;
    color: #727272;
    text-align: center;
}

.bachelor-logo {
    /* width: 50%;
    margin-top: -10px;*/
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    position: absolute;
}

.navbar-buttons {
    margin-top: 10px;
}

.navbar-buttons li {
    padding-right: 20px;
}

@media only screen and (max-width: 767px) {
    .bachelor-logo {
        margin-left: 5.2em;
        padding-bottom: 5px;
    }
    .navbar-buttons {
        margin-top: -20px;
        margin-left: 10em;
        display: inline-block;
    }
}

.score {
    margin-top: 12px;
}

.gameOverGif {
    display: block;
    text-align: center;
}

.gameOverTitle {
    text-align: center;
    font-family: cursive;
}

.pictures {
    width: 200px;
    height: 200px;
    overflow: hidden;
}

.pictures:hover {
    -webkit-transform: rotate(-10deg);
    -ms-transform: rotate(-10deg);
    transform: rotate(-10deg);
}

img.Ben {
    max-width: 100%;
    margin: 0 0 0 0;
    padding-right: 0px;
}

img.Jojo {
    max-height: 100%;
    margin: 0 0 0 -140px;
}

img.Nick {
    max-height: 100%;
    margin: 0 0 0 -140px;
}

img.Rachel {
    max-height: 100%;
    margin: 0 0 0 -145px;
}

.modelPic {
    display: block;
    overflow: hidden;
    text-align: center;
    margin: auto;
    width: 300px;
    height: 300px;
}

.Benoverlay {
    max-width: 100%;
    margin: 0 0 -200px 0;
}

.Jojooverlay {
    max-height: 100%;
    margin: 0 -300px 0;
}

.Nickoverlay {
    max-height: 100%;
    margin: 0 -275px 0;
}

.Racheloverlay {
    max-height: 100%;
    margin: 0 -100px -100px -220px;
}

.centerText {
    text-align: center;
    font-size: 20px;
    font-family: cursive;
}

.button-wrapper {
    margin-bottom: 5px;
}

.grid {
    width: 900px;
    margin: auto;
}

.gridBox {
    border: 5px solid #DCDCDC;
}

.roseBackground {
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
}

.rosePic {
    opacity: 0.5;
    width: 100%;
    height: 100px;
}

.resetButton {
    width: 50px;
    height: 50px;
    background-color: grey;
    border-radius: 50%;
}

.navOptions {
    left: 85%;
    top: 50%;
    -webkit-transform: translate(-85%, -50%);
    -ms-transform: translate(-85%, -50%);
        transform: translate(-85%, -50%);
    position: absolute;
    font-family: Arial;
    font-size: 20px;
}

.correct {
    opacity: 0.4;
    pointer-events: none;
}

.incorrect {
    opacity: 0.4;
    pointer-events: none;
}


